import Form from "react-bootstrap/Form";
import React, {useEffect, useState} from "react";
import CanvasJSReact from "@canvasjs/react-charts";
const CanvasJSChart = CanvasJSReact.CanvasJSChart;


import {
  CAvatar,
  CButton,
  CButtonGroup,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CProgress,
  CRow,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
} from '@coreui/react'

import { cilArrowRight, cilArrowLeft } from '@coreui/icons'
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import {useForm} from "react-hook-form";

import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css'

import {errorToast, getCookieValue, ipAddress, successToast, tokenName} from "../../utils/Common";
import axios from "axios";
import Table from "react-bootstrap/Table";
import { MdKeyboardArrowRight } from "react-icons/md";
import CountUp from "react-countup";

const SymboAccount = () => {
  let [loader, setLoader] = useState(true)

  useEffect(() => {
    adminShowAllSymboAccountInfo();
    adminShowSymboBalance();
  }, []);

  const [accountInfo, setAccountInfo] = useState([]);
  const [totalBalance, setTotalBalance] = useState([]);

  const today = new Date();

  const month = String(today.getMonth() + 1).padStart(2, '0');
  const year = today.getFullYear();
  const date = String(today.getDate()).padStart(2, '0');
  const currentDate = year + "-" + month + "-" + date;

  function convert(x) {
    x=x.toString();
    var lastThree = x.substring(x.length-3);
    var otherNumbers = x.substring(0,x.length-3);
    if(otherNumbers != '')
      lastThree = ',' + lastThree;
    var res = otherNumbers.replace(/\B(?=(\d{2})+(?!\d))/g, ",") + lastThree;
    return res;
  }

  function adminShowAllSymboAccountInfo() {
    let token = getCookieValue(tokenName);
    // console.log(token)
    axios.get(ipAddress + "admin/view-symbo-account", {
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content_Type': 'application-json',
      }
    })
      .then((response) => {
        setAccountInfo(response.data.recordset);
        // localStorage.setItem("recordset", JSON.stringify(response.data.recordset));
      }).catch(e => {
      console.log(e)
    })
  }

  function adminShowSymboBalance() {
    let token = getCookieValue(tokenName);
    // console.log(token)
    axios.get(ipAddress + "admin/symbo-balance", {
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content_Type': 'application-json',
      }
    }).then((response) => {
        setTotalBalance(response.data.recordset[0].final_amount);
      setLoader(false)
      }).catch(e => {
      console.log(e)
      setLoader(false)
    })
  }

  let {register, handleSubmit, reset ,formState: {errors}} = useForm();

  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => { setShow(true); }

  function onSubmit(Param) {

    var amt = document.getElementById("amount").value;

    var type = document.getElementById("type").value;

    if((totalBalance < amt) && type === "Withdraw")
    {
      errorToast("Your withdraw value exceeds the available balance");
    }else
    {
      let token = getCookieValue(tokenName);
      axios.post(ipAddress + "admin/symbo-account", Param, {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content_Type': 'application-json',
        }
      })
        .then((response) => {
          if (response.data.error === false) {
            successToast(response.data.message);
            adminShowAllSymboAccountInfo();
            adminShowSymboBalance();
            reset();
            handleClose();
          } else {
            errorToast("Error while Adding");
          }
        }).catch(e =>{
        console.log(e)
      })
    }


  }

  const [currentPage, setCurrentPage] = useState(1);
  // const itemsPerPage = 5;
  const itemsPerPage = 10;

  const totalPages = Math.ceil(accountInfo.length / itemsPerPage);
  const pageLimit = 5; // Set the limit of displayed page buttons

  const handleClick = (number) => {
    setCurrentPage(number);
  };

  const handleNextPage = () => {
    setCurrentPage((prevPage) => Math.min(prevPage + 1, totalPages));
  };

  const handlePrevPage = () => {
    setCurrentPage((prevPage) => Math.max(prevPage - 1, 1));
  };

  // Calculate the range of page numbers to display
  const pageNumbers = [];
  const maxButtonsToShow = 5;

  let startPage = currentPage - Math.floor(maxButtonsToShow / 2);
  startPage = Math.max(1, startPage);

  let endPage = startPage + maxButtonsToShow - 1;
  endPage = Math.min(totalPages, endPage);

  if (endPage - startPage + 1 < maxButtonsToShow) {
    // Adjust startPage when there are not enough buttons to display
    startPage = Math.max(1, endPage - maxButtonsToShow + 1);
  }

  for (let i = startPage; i <= endPage; i++) {
    pageNumbers.push(i);
  }

    return(
      <>
        {loader ? <div>
            <span className="spinner-border text-primary"></span>
          </div> :
          <>
            <CCard className="mb-4">
            <CCardBody>
              <CRow>
                <div className="table-responsive px-3 ">
                  <h2 style={{ textAlign: 'center' }} > <b>Total Balance : {'₹ '}<CountUp start={0} end={totalBalance} /> </b> </h2>
                  <hr/>
                  <div className="bg-light py-2 mb-3" >
                    <Button variant="primary" onClick={handleShow} >
                      Add New Payment
                    </Button>

                    <div className="row" >

                    </div>

                  </div>

                  <div className="row">

                    <br/>

                    <div style={{height: "335px"}} className="table-responsive">
                      <Table responsive hover>
                        <thead style={{position: "sticky", top: 0}}>
                        <tr>
                          <th>Transaction ID</th>
                          <th>Created On</th>
                          <th>Mode Of Payment</th>
                          <th>Date of Payment</th>
                          <th>Amount</th>
                          <th>Type</th>
                        </tr>
                        </thead>

                        <tbody>
                        {accountInfo.slice((currentPage - 1) * itemsPerPage, currentPage * itemsPerPage).map((item, index) => (
                          <tr key={index}>
                            <td>{item.id}</td>
                            <td>{item.date.split("T")[0]}</td>
                            <td>{item.modeOfPayment}</td>
                            <td>{item.dateOfPayment.split("T")[0]}</td>
                            {
                              item.transactionType === "Deposit" ?
                                <td style={{color: 'green'}}>{convert(item.amount)}</td>
                                :
                                <td style={{color: 'red'}}>{convert(item.amount)}</td>
                            }
                            {
                              item.transactionType === "Deposit" ?
                                <td><span className="badge bg-success ms-auto"> {item.transactionType} </span></td>
                                :
                                <td><span className="badge bg-danger ms-auto"> {item.transactionType} </span></td>

                            }
                          </tr>
                        ))
                        }
                        </tbody>

                      </Table>

                    </div>

                    <div className="text-center">
                      <button className="btn btn-sm me-2" style={{fontSize: '18px', border: 'none'}} onClick={handlePrevPage}
                              disabled={currentPage === 1}>
                        &laquo;
                      </button>

                      {pageNumbers.map((number) => (
                        <button key={number}
                                className={`btn btn-sm ${currentPage === number ? 'btn-success' : 'btn-light'}`}
                                onClick={() => handleClick(number)}>
                          {number}
                        </button>
                      ))}

                      <button className="btn btn-sm ms-2" style={{fontSize: '18px', border: 'none'}} onClick={handleNextPage}
                              disabled={currentPage === totalPages}>
                        &raquo;
                        {/*<cilArrowRight/>*/}
                      </button>
                    </div>
                  </div>

                </div>
              </CRow>
            </CCardBody>
          </CCard>

          <Modal show={show} onHide={handleClose} centered>
            <Modal.Header closeButton>
              <Modal.Title>Add Payment</Modal.Title>
            </Modal.Header>
            <Modal.Body>

              <form onSubmit={handleSubmit(onSubmit)}>
                <div className="mb-3">
                  <label htmlFor="mop">Mode of Payment</label>

                  <select {...register('mop', {required: true})} className="form-control" id="mop">
                    <option value=""> Choose Mode of Payment</option>
                    <option value="Credit_Card"> Credit Card</option>
                    <option value="Debit_Card">Debit Card</option>
                    <option value="Net_Banking">Net Banking</option>
                    <option value="UPI">UPI</option>
                  </select>

                  {errors.mop && <p className='text-danger'>This is a required field.</p>}
                </div>

                <div className="mb-3">
                  <label htmlFor="amount">Amount</label>
                  <input {...register('amount', {required: true})} pattern = "[0-9]{1,9}" placeholder="Enter Amount" type="text"
                         id="amount"
                         className="form-control"/>

                  {errors.amount && <p className='text-danger'>This is a required field.</p>}
                </div>

                <div className="mb-3">
                  <label htmlFor="date">Date of Payment</label>
                  <input {...register('date', {required: true})} placeholder="Date of Payment" type="date" id="date"
                         max={currentDate}
                         min="1980-01-01"
                         className="form-control"/>

                  {errors.date && <p className='text-danger'>This is a required field.</p>}
                </div>

                <div className="mb-3">
                  <label htmlFor="type">Transaction Type</label>

                  <select {...register('type', {required: true})} className="form-control" id="type">
                    <option value=""> Transaction Type</option>
                    <option value="Deposit">Deposit</option>
                    <option value="Withdraw">Withdraw</option>
                  </select>


                  {errors.type && <p className='text-danger'>This is a required field.</p>}
                </div>

                <div className="mb-3">
                  <label htmlFor="Remarks">Remarks</label>
                  <textarea {...register('Remarks', {required: true})} placeholder="Remarks" id="Remarks"
                            className="form-control">
                </textarea>

                  {errors.Remarks && <p className='text-danger'>This is a required field.</p>}
                </div>

                <hr/>

                <div className="mb d-flex justify-content-between align-items-center">
                  <Button variant="secondary" onClick={handleClose}>
                    Close
                  </Button>

                  <Button variant="primary" type="submit" className="ml-auto">
                    Add
                  </Button>
                </div>


              </form>

            </Modal.Body>

            {/*<Modal.Footer>*/}
            {/*  <Button variant="secondary" onClick={handleClose}>*/}
            {/*    Close*/}
            {/*  </Button>*/}
            {/*  <Button variant="primary" onSubmit={handleSubmit(onSubmit)} >*/}
            {/*    Save Changes*/}
            {/*  </Button>*/}
            {/*</Modal.Footer>*/}

          </Modal>
        </>}
      </>
    )
}

export default SymboAccount;
